@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;600&family=Vidaloka&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Vidaloka&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background: rgb(214, 253, 231);
  /* background: rgb(190, 255, 218); */
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style-type: none;
}

.main-container {
  width: 97%;
  height: 100%;
  margin: 0 auto;
}
.form-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
    position: relative;
    border-radius: 20px;
}

textarea {
    resize: none;
    height: 200px;
    border: none;
    padding: 5px;
    font-family: "Poppins", 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

input[name="title"] {
    height: 70px;
    padding-left: 20px;
    font-family: "Vidaloka", serif;
    font-size: 3rem;
    border: none;
}

.inputs {
    border: none;
    height: 35px;
    padding: 5px;
    border-bottom: 1px solid #ccc;
    width: 300px;
}

input[type=file] {
    display: none;
}

.features-menu {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    border-radius: 50%;
    border: none;


}

.features-descr {
    position: absolute;
    font-size: 1rem;
    background: #555;
    border-radius: 8px;
    padding: 10px;
    color: #fff;
    width: 250px;
    opacity: 0;
    top: 20px;
    left: 20px;
    transition: opacity 0.4s ease;
}

.features-menu:hover~.features-descr {
    opacity: 1;
}

.menu-container {
    display: flex;

}

button {
    cursor: pointer;
}

.fa-plus-circle {
    color: #666;
}

.add-features {
    transition: transform 0.4s ease;
    transform: translateX(-200px) scale(0);
    width: 240px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
}

.scale {
    transform: translateX(0px) scale(1);
}

.features-btn {
    font-size: 1.5rem;
    width: 50px;
    height: 50px;
    border: none;
    color: #111;
    border: 1px solid #444;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

}

@-webkit-keyframes slideDown {
    0% {
        transform: scale(0px)
    }

    100% {
        transform: scale(1px)
    }
}

@keyframes slideDown {
    0% {
        transform: scale(0px)
    }

    100% {
        transform: scale(1px)
    }
}

.sub-container {
    overflow: hidden;
}

.btn-author,
.btn-descr,
.btn-cat,
.btn-image {
    position: absolute;
    top: 15%;
    background: #555;
    border-radius: 8px;
    padding: 5px;
    color: #fff;
    width: 100px;
    opacity: 1;
    border-radius: 10px;
    scale: 0;
    width: 100px;
}

.btn-descr {
    left: 20px;
}


.btn-cat {
    left: 120px;
}

.btn-author {
    left: 60px;
}

.author-hover:hover~.btn-author {
    scale: 1;
}

.category-hover:hover~.btn-cat {
    scale: 1;
}

.description-hover:hover~.btn-descr {
    scale: 1;
}

.image-hover:hover~.btn-image {
    scale: 1;
}
.side-bar {
    width: 100%;
    height: 100%;
    grid-column: 2/3;
    grid-row: 1/2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.card {
    background: #fff;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    margin-top: 30px;
    box-shadow: 10px 10px 60px #777;
    border-radius: 13px;
}
.social-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.social-group {
    padding: 10px 0px;
    display: flex;
    flex-direction: column;
    width: 90%;
    height: 100%;
    align-items: center;
    justify-content: space-evenly;
    background: rgb(214, 253, 231);
    border-radius: 13px;
    margin: 10px;
}

.social-group>a {
    font-size: 4rem;
    transition: all 0.4s;
}


.social-group>a:hover i {
    color: rgb(102, 102, 102);
    -webkit-animation: bounce;
            animation: bounce;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

@-webkit-keyframes bounce {
    10% {
        transform: translateX(-20px);
    }

    20% {
        transform: translateY(10px);
    }

    30% {
        transform: translateX(-20px);
    }

    40% {
        transform: translateX(20px);
    }

    50% {
        transform: translateY(-10px);
    }

    100% {
        transform: translateX(0px)
    }

}

@keyframes bounce {
    10% {
        transform: translateX(-20px);
    }

    20% {
        transform: translateY(10px);
    }

    30% {
        transform: translateX(-20px);
    }

    40% {
        transform: translateX(20px);
    }

    50% {
        transform: translateY(-10px);
    }

    100% {
        transform: translateX(0px)
    }

}
nav {
    width: 100%;
    margin-top: 10px;
    height: 300px;
    flex-direction: column;
    display: flex;
    /* box-shadow: 10px 30px 60px #999; */
    align-items: center;
    justify-content: space-around;
    background: #fff;

}

.heading {
    font-size: 5rem;
    font-family: 'Vidaloka', serif;
}

.heading-bottom {
    display: flex;
    width: 90%;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid lightslategrey;
    height: 60px;
    padding-top: 25px;
}

.heading-bottom-right {
    display: flex;
    align-items: center;
}

ul {
    display: flex;
    justify-content: center;
    align-items: center;
}

.list-items {
    list-style: none;
    font-size: 1.2rem;
    margin: 0px 10px;
    position: relative;
    overflow: hidden;

}

.search-panel {
    display: flex;
    align-items: center;
}

.search-panel>button {
    border: none;
    margin-left: 5px;
    position: relative;
    right: 12px;
    height: 45px;
    padding-left: 10px;
    font-size: 1.3rem;
    cursor: pointer;
    background: #fff;
}

.search-input {
    border: none;
    background: rgb(218, 218, 218);
    height: 45px;
    width: 0px;
    padding-left: 7px;
    transition: all 0.3s ease;
    font-size: 1.2rem;


}

.active {
    width: 400px;
}

