.link-active {
    font-weight: 900;
}

.user-icon {
    font-size: 2rem;
    position: relative;
}

.fa-user-tie:after {
    content: "Write a new Story";
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen';
    font-size: 1rem;
    background: #666;
    color: #fff;
    position: absolute;
    bottom: 120%;
    margin-top: 2px;
    opacity: 0;
    right: -30px;
    width: 200px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
}

.point {
    position: absolute;
    bottom: 80%;
    left: 20px;
    width: 20px;
    height: 20px;
    opacity: 0;
    border-bottom: 15px solid #666;
    border-right: 20px solid transparent;
    border-left: 2px solid transparent;
    transform: rotateX(180deg);
}

.user-icon:hover .fa-user-tie:after {
    opacity: 1;
}

.user-icon:hover .point {
    opacity: 1;
}
.container {
    /*  display: flex;
    justify-content: space-between;
    margin: 20px 0; */
    display: grid;
    grid-template-columns: 1fr 25%;

}
.blog-post {
    /* width: 100%; */
    grid-column: 1/2;
    grid-row: 1/2;
}

.save-to-favorite-btn {
    border: none;
    color: #111;
    cursor: pointer;
}

.crimson {
    color: crimson;
}

.post-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #ccc;
}

.margin-top {
    margin-top: 30px;
}

.post-header {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-evenly;
}

.post-title {
    font-family: "Vidaloka", serif;
    font-size: 3.3vw;
    text-align: center;
}

.post-image {
    object-fit: contain;
    margin: 10px 0px;
    height: 300px;
    width: 100%;
}

.post-preview {
    font-size: 1.2rem;
    padding: 5px;
    margin-bottom: 10px;
    width: 100%;
    max-width: 600px;
    text-align: center;
}

.author {
    color: #777;
    text-align: center;
    margin: 10px auto;
    padding-top: 10px;
    border-top: 1px solid #444;
    width: 300px;
}

.paragraph {
    font-size: 1.3rem;
    text-align: justify;
    width: 70%;
    margin: 20px 0px;
}

.related-container {
    width: 100%;
    height: 100%;
}

.related-header {
    width: 100%;
    height: 100px;
    padding: 10px 0px;
    background: #fff;
    display: flex;
    border-radius: 13px;
    margin: 30px 0px;
    box-shadow: 10px 10px 40px #ccc;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.related-header>i {
    font-size: 2.5rem;
    -webkit-animation: point infinite alternate .5s;
            animation: point infinite alternate .5s;
}

@-webkit-keyframes point {
    0% {
        transform: translateY(-10px)
    }

    100% {
        transform: translateY(10px)
    }
}

@keyframes point {
    0% {
        transform: translateY(-10px)
    }

    100% {
        transform: translateY(10px)
    }
}


.related-header>h3 {
    font-family: "Vidaloka", serif;
    font-size: 2vw;
    color: crimson;
}

.related-image {
    width: 100%;
}

.sidebar-related-posts {
    display: flex;
    flex-direction: column;
    position: relative;
    bottom: 20px;
}

.related-title {
    font-size: 2.2vw;
    font-family: "Vidaloka", serif;
    padding: 10px;
    border-bottom: 1px solid #777;
    margin-bottom: 10px;
    text-align: center;
}
.saved-posts-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
}

.saved-posts-container>h2 {
    font-family: "Vidaloka", serif;
    font-size: 2rem;
}

.pinned-posts {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /* background: rgb(214, 253, 231); */
}

.pinned-posts>p {
    text-align: center;
}

.pinned-container {
    width: 300px;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.pinned-image {
    width: 100%;
}

.pinned-title {
    font-family: "Vidaloka", serif;
    text-align: center;
    margin: 0px auto;
    padding: 15px 0px;
    border-bottom: 1px solid #999;
    width: 90%;
}

.margin {
    margin: 10px;
}

.articles-count {
    font-size: 1.3rem;
    padding: 10px;
}
.no-pinned-container {
    width: 300px;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background: rgb(156, 248, 196); */
    background: #fff;
    margin: 130px auto;
    border-radius: 15px;
    box-shadow: 12px 12px 16px 0 #777,
        -8px -8px 12px 0 #fff;
}

.no-pinned-text {
    font-size: 1.3rem;
    margin-bottom: 10px;
}
